  getters: {
    getColumns(state) {
      let arr = new Array(state.mainColumns).fill(1);
      for(let i = 0; i < arr.length; i++)
        arr[i] = [];
      let x = 0;
      let conf = 0;
      for(let i = 0; x < state.posts.length && new Date().getMonth() < 4 ; i += state.mainColumns) {
        for(let j = 0; j < state.mainColumns && x < state.posts.length; j++) {
          if(state.mainColumns > 1 && arr[j].length && arr[j][arr[j].length - 1].conference && conf < state.mainColumns) {
            conf++;
            continue;
          }
          arr[j].push(state.posts[x++]);
        }
      }
      return arr;
    },
  },
  mutations: {
    setRegistration(state) {
      state.isRegistered = true;
    },
    setLogin(state) {
      state.isRegistered = false;
    },
    acceptPrivacyPolicy(state) {
      state.privacyPolicy = !state.privacyPolicy;
    },
    setMainColumns(state, width) {
      if(width >= 1200)
        state.mainColumns = 3;
      if(width < 1200)
        state.mainColumns = 2;
      if(width < 767) 
        state.mainColumns = 1;
    },
    setStateVote(state, index) {
      state.vote = state.vote.map((el, i) => el = i == index);
    },
    createStateVote(state, arr) {
      state.vote = arr;
    },
    setAudioHandleTime(state, sec) {
      state.audioHandleTime = sec;
    }
  },
